@import url(https://fonts.googleapis.com/css?family=Montserrat:200,300,400,500,700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  letter-spacing: 1px; }

* {
  font-family: inherit;
  box-sizing: border-box;
  outline: none; }

div.container {
  width: 100%;
  min-height: calc(100vh - 230px); }

header {
  width: 100%;
  height: 50px;
  background: linear-gradient(107deg, #4d76ff 0%, #0f3f8c 100%); }
  header div.wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1200px;
    width: 100%;
    height: 100%;
    margin: auto;
    padding: 0 15px; }
    header div.wrapper div.brand {
      display: flex;
      align-items: center; }
      header div.wrapper div.brand img {
        height: 32px;
        margin: 0 15px 5px 0; }
      header div.wrapper div.brand h1 {
        color: white;
        font-size: 24px;
        font-weight: 300;
        letter-spacing: 2px;
        margin: 0 0 2.5px 0; }
    header div.wrapper div.menu {
      display: flex;
      align-items: center;
      height: 100%; }
      header div.wrapper div.menu a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 120px;
        height: 100%;
        color: white;
        text-decoration: none;
        font-size: 18px;
        font-weight: 300;
        letter-spacing: 2px; }
        header div.wrapper div.menu a:hover {
          border-bottom: 5px solid white; }

footer {
  width: 100%;
  min-height: 180px;
  background: #f2f2f2; }
  footer div.wrapper {
    width: 100%;
    max-width: 1200px;
    margin: auto;
    padding: 15px; }
  footer div.fhead {
    display: flex;
    align-items: center; }
    footer div.fhead img {
      height: 24px;
      margin: 0 15px 0 0; }
  footer div.menu-items {
    display: flex;
    flex-wrap: wrap; }
    footer div.menu-items a {
      display: flex;
      align-items: center;
      width: 50%;
      height: 40px;
      color: black; }

div.api-wrap {
  width: 100%;
  max-width: 1200px;
  min-height: calc(100vh - 230px);
  padding: 15px;
  margin: auto; }

h3 {
  font-size: 24px;
  font-weight: 300;
  margin: 0 0 30px 0; }

h4 {
  font-size: 18px;
  font-weight: 400;
  margin: 0 0 15px 0;
  font-family: monospace; }

h5 {
  font-size: 18px;
  font-weight: 700;
  margin: 0 0 15px 0; }

p {
  font-size: 14px;
  margin: 0 0 5px 0; }
  p.label {
    font-weight: bold; }

div.page-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 1200px;
  min-height: calc(100vh - 230px);
  margin: auto;
  padding: 15px; }

div.faucet {
  width: 100%; }
  div.faucet h2 {
    font-size: 24px;
    font-weight: 300;
    margin: 0 0 10px 0; }
  div.faucet h3 {
    display: flex;
    align-items: center;
    font-size: 18px;
    font-weight: 400;
    margin: 0 0 15px 0; }
    div.faucet h3 div {
      display: flex;
      align-items: center;
      margin: 0 10px;
      font-weight: bold; }
      div.faucet h3 div img {
        height: 18px;
        margin: 0 10px 0 0; }
  div.faucet p {
    font-size: 14px;
    font-weight: 700;
    margin: 0 0 10px 0; }
  div.faucet div.inline-input {
    display: flex;
    align-items: center;
    position: relative; }
  div.faucet input {
    width: 100%;
    max-width: 640px;
    height: 50px;
    font-size: 18px;
    border-radius: 5px;
    border: none;
    background: #f2f2f2;
    padding: 0 15px;
    margin: 0 0 15px 0; }
  div.faucet a.button {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 0 15px 15px;
    width: 180px;
    height: 50px;
    border-radius: 5px;
    background: linear-gradient(107deg, #4d76ff 0%, #0f3f8c 100%);
    color: white;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer; }
  div.faucet div.message {
    position: absolute;
    left: 0;
    top: 65px;
    background: #d9d9d9;
    padding: 15px;
    width: 100%;
    max-width: 640px;
    height: 50px;
    border-radius: 5px;
    font-size: 14px;
    font-weight: bold;
    -webkit-transition: opacity 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53);
    transition: opacity 0.25s cubic-bezier(0.55, 0.085, 0.68, 0.53);
    opacity: 0;
    pointer-events: none; }
    div.faucet div.message.active {
      opacity: 1; }

